import React from 'react'
import Sighting from './Sighting';

const Sightings = ({ sightings }) => {
  return (
     <div className='container'>
        {sightings.map(s => <Sighting key={s.id} sighting={s} />)}
     </div> 
  )
}

export default Sightings


