import React from 'react';
import { Form, Button, Modal, Input } from 'semantic-ui-react'

const AddForm = ({ species, onAdd, shown, close, handleCountChange, 
  handleDateTimeChange, handleDescriptionChange, handleSpeciesChange}) => {

  if (shown === true) {
    return (
      <Modal size='tiny' open={shown} onClose={close()}>
      <Modal.Content>
           <div className='adding'>
            <h3>add sighting</h3>
            <Form onSubmit={onAdd}>
              <Form.Field required label='Species' control='select' onBlur={handleSpeciesChange}>
                {species.map(s => <option key={s.name} value={s.name}>{s.name}</option>)}
              </Form.Field>
              <Form.Input required type='datetime-local' placeholder='dd/mm/yyyy, hh:mm' label='Date' onBlur={handleDateTimeChange} />
              <Form.Input required type='number' label='Count' onBlur={handleCountChange} />
              <Form.TextArea label='Description' maxLength="250" onBlur={handleDescriptionChange}/>
              <Button id='submit' type='submit'>add</Button>
            </Form> 
          </div>
          </Modal.Content>
      </Modal>   
    )
  } else {
    return (
      <div></div>
    )
  } 
}

export default AddForm



