import React from 'react';
import axios from 'axios'
import Sightings from './components/Sightings';
import AddForm from './components/AddForm';
import AppMenu from './components/AppMenu';
import duckService from './services/sightings'

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            sightings: [],
            species: [],
            shown: false,
            sort: false,
            newSpecies: '',
            newDescription: '',
            newCount: '',
            newDateTime: ''
        }

        this.handleSpeciesChange = this.handleSpeciesChange.bind(this);
        this.handleDescriptionChange = this.handleDescriptionChange.bind(this);
        this.handleDateTimeChange = this.handleDateTimeChange.bind(this);
        this.handleCountChange = this.handleCountChange.bind(this);
    }

/*     componentWillMount() {
        duckService
            .getAll()
            .then(response => {
                this.setState({sightings: response})
                return axios.getAll('http://localhost:8081/species')
            })
            .then(response => {
                this.setState({species: response.data})
            })
    } */

    componentWillMount() {
        axios.all([
            axios.get('/sightings'),
            axios.get('/species')
        ])
        .then(axios.spread((sights, specs) => {
            this.setState({
                species: specs,
                sightings: sights
            })
        }))
/*         return () => {
            this.setState({
                species: specs,
                sightings: sights
            })
        } */
    }

    
    

/*     getSpecies = () => {
        const promise = axios.get('http://localhost:8081/species')
        promise.then(response => {
            this.setState({species: response.data})
        })
    } */


    addSighting = (event) => {
        event.preventDefault()
        if (this.validateForm()) {
            let date = new Date(this.state.newDateTime);
            let isostring = date.toISOString();
            let amount = parseInt(this.state.newCount, 10);

            const duckObject = {
                species: this.state.newSpecies,
                description: this.state.newDescription,
                dateTime: isostring,
                count: amount
            }

            duckService
                .create(duckObject)
                .then(response => {
                    this.setState({
                        sightings: this.state.sightings.concat(response),
                        newSpecies: '',
                        newCount: '',
                        newDateTime: '',
                        newDescription: '',
                        shown: false
                    })
                })
        }
    }

    validateSpecies = () => {
        for (let i = 0; i < this.state.species.length; i++) {
            if (this.state.species[i].name === this.state.newSpecies) {
                return true;
            }
        }
        return false;
    }

    validateForm = () => {
        console.log(this.state.species)
        if (isNaN(this.state.newCount)) {
            alert("Only input numbers for the sighting's count.")
            return false;
        } else if (this.state.newDescription > 250) {
            alert("Please limit your description to 250 characters.")
            return false;
        } else if (this.state.newDateTime.length > 17) {
            alert("Check that date and time are correct.")
            return false;
        } else if (parseInt(this.state.newCount, 10) < 1 ) {
            alert("Check that count is correct.")
            return false;
        } 
        return true;
    }

    changeSort = () => { 
        if (this.state.sort === false) {
            return () => {
                this.setState({
                    sort: true
                })
                console.log(this.state.sightings) 
            }
        } else if (this.state.sort === true) {
            return () => {
                this.setState({
                    sort: false
                })
                console.log(this.state.sightings) 
            }
        }
            
    }

    changeShown = () => { 
        let list = [...this.state.sightings]
        if (this.state.shown === false) {
            return () => {
                this.setState({
                    shown: true
                })
            }
        } else if (this.state.shown === true) {
            return () => {
                this.setState({
                    shown: false
                })
            }
        }     
    }

    handleSpeciesChange(event) {
        this.setState({ newSpecies: event.target.value })
    }

    handleDescriptionChange(event) {
        this.setState({ newDescription: event.target.value })
    }

    handleCountChange(event) {
        this.setState({ newCount: event.target.value })
    }

    handleDateTimeChange(event) {
        this.setState({ newDateTime: event.target.value })
    }
 
/*     sortAsc() {
        let list = [...this.state.sightings]
        list.sort(function(a, b) {
            return a.dateTime - b.dateTime;
        });
    }

    sortDesc() {
        let list = [...this.state.sightings]
        list.sort(function(a, b) {
            return b.dateTime - a.dateTime;
        });
    }  */


    render() {
        //let list = [...this.state.sightings]
        //this.getSpecies();


        return (
        <div className="App">
            <AppMenu 
                shown={this.changeShown} 
                sort={this.changeSort} 
                sortName={this.state.sort ? "large sort numeric descending icon" : "large sort numeric ascending icon"}
                shownName={this.state.shown ? "large minus icon" : "large plus icon"}
            />
            <AddForm 
            shown={this.state.shown} 
            species={this.state.species} 
            onAdd={this.addSighting} 
            close={this.changeShown}
            handleCountChange={this.handleCountChange}
            handleDateTimeChange={this.handleDateTimeChange}
            handleDescriptionChange={this.handleDescriptionChange}
            handleSpeciesChange={this.handleSpeciesChange}
            />
            <Sightings sightings={this.state.sightings}/>
        </div>
        );
    }
}

export default App;



